<template>
  <div>
    <div class="ui active inverted dimmer" v-if="loading">
      <div class="ui loader"></div>
    </div>
    <div v-else>
      <admin-menu v-if="connect()"></admin-menu>
      <div class="" style="padding-top:80px">
        <transition name="custom-classes-transition"
          enter-active-class="animated fadeIn"
          leave-active-class="animated fadeOut"
          mode="out-in">
          <router-view></router-view>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
  import adminMenu from './components/Global/Auth/AdminMenu'
  import vuex from 'vuex'
  var localStorage = require('web-storage')().localStorage 
  export default {
    data() {
      return {
        showMenu: true,
        loading: true,
        loaded: false,
        connect : function() {
          if (localStorage.get("authUser")) {
            return true
          }else{
            return false
          }
        }
      };
    },
    components: {
      adminMenu
    },
    computed:{
      ...vuex.mapState(['userStore'])
    },
    created () {
      
      let data = {}
      data.name = "testtakal"
      data.lat = "testtakal"
      this.loading = false
      this.loaded = true
      //this.$http.post("http://localhost:8000/api/Puit",data).then(data1 => {
      //  console.log(data1);
      //},data =>{
      //  console.log(data.body);
      //})
    }

  }
</script>

<style src="./animate.css"></style>



