<template lang="html">
  <div class="">
    <navbar>
      <navbar-item :link="{name: 'site'}" name="Site"></navbar-item><!--name: 'site'  represente la route -->
      <navbar-item :link="{name: 'analyse'}" name="Analyse"></navbar-item>
      <navbar-item :link="{name: 'laboratoire'}" name="Laboratoire"></navbar-item>
      <navbar-item :link="{name: 'statistiques'}" name="Statistiques"></navbar-item>   
      <div class="right menu">
        <div class="ui dropdown item">{{user}}<i class="dropdown icon"></i>
          <div class="menu">
            <a class="item">Editer</a>
            <a class="item" @click="logout">Se deconnecter</a>
          </div>
        </div>
      </div>
    </navbar>
  </div>
</template>

<script>
import navbar from '../../Core/Navbar'
import navbarItem from '../../Core/NavbarItem'
var localStorage = require('web-storage')().localStorage
export default {
  data() {
    return {
      user:null
    };
  },
  computed: {},
  ready() {},
  attached() {},
  methods: {
    logout(){
      localStorage.remove('authUser')
      this.$router.push({name: 'login'})
    }
  },
  components: {
    navbar,navbarItem
  },
  mounted: function (){
    $('.ui .dropdown').dropdown()
    const userData = localStorage.get('authUser');
    this.user = userData.name;
  }
};
</script>

<style lang="css">
</style>



