import VueRouter from 'vue-router'
var localStorage = require('web-storage')().localStorage


const router = new VueRouter({
  mode: 'hash',
  routes:[{
      name: 'root',
      path: '/',
      meta: {requiresAuth: false},
      component: resolve => require(['./components/Global/Home.vue'],resolve)
  },{
    name: 'login',
    path: '/login',
    component: resolve => require(['./components/Global/Login.vue'],resolve)
  },{
      name: 'site',
      path: '/admin/site',
      meta: {requiresAuth: true},
      component: resolve => require(['./components/Global/Auth/Site.vue'],resolve),
      children: [{
          name: 'site.index',
          path: '/admin/site/index',
          meta: {requiresAuth: true},
          component: resolve => require(['./components/Global/Auth/Site/Index.vue'],resolve),
        },{
            name: 'site.add',
            path: '/admin/site/add',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Site/Add.vue'],resolve),
        },{
            name: 'site.show',
            path: '/admin/site/:id(\\d+)',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Site/Show.vue'],resolve),
        },{
            name: 'puit.add',
            path: '/admin/site/:id(\\d+)/puit/add',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Puit/Add.vue'],resolve),
        },{
            name: 'puit.show',
            path: '/admin/site/:id(\\d+)/puit/:idpuit(\\d+)',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Puit/Show.vue'],resolve),
        },{
            name: 'analyse.add',
            path: '/admin/site/:id(\\d+)/puit/:idpuit(\\d+)/echan/:idech(\\d+)',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Analyse/Add.vue'],resolve),
        }
      ]
  },{
      name: 'analyse',
      path: '/admin/analyse',
      meta: {requiresAuth: true},
      component: resolve => require(['./components/Global/Auth/Analyse.vue'],resolve),
      children: [{
        name: 'typeanalyse.index',
        path: '/admin/analyse/type_analyse',
        meta: {requiresAuth: true},
        component: resolve => require(['./components/Global/Auth/TypeAnalyse/Index.vue'],resolve),
      },{
        name: 'typeanalyse.add',
        path: '/admin/analyse/add',
        meta: {requiresAuth: true},
        component: resolve => require(['./components/Global/Auth/TypeAnalyse/Add.vue'],resolve),
      },{
        name: 'typeanalyse.show',
        path: '/admin/analyse/type_analyse/:id(\\d+)',
        meta: {requiresAuth: true},
        component: resolve => require(['./components/Global/Auth/TypeAnalyse/Show.vue'],resolve),
      }
    ]},{
          name: 'laboratoire',
          path: '/admin/laboratoire',
          meta: {requiresAuth: true},
          component: resolve => require(['./components/Global/Auth/Laboratoire.vue'],resolve),
          children: [{
            name: 'laboratoire.index',
            path: '/admin/laboratoire/index',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Laboratoire/Index.vue'],resolve),
          },{
            name: 'laboratoire.add',
            path: '/admin/laboratoire/add',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Laboratoire/Add.vue'],resolve),
          }]
    },{
        name: 'statistiques',
        path: '/admin/statistiques',
        meta: {requiresAuth: true},
        component: resolve => require(['./components/Global/Auth/Statistiques.vue'],resolve),
          children: [{
            name: 'statistiques.resultats',
            path: '/admin/statistiques/resultats',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Statistiques/AddResultats.vue'],resolve),
          },{
            name: 'statistiques.ternaire',
            path: '/admin/statistiques/ternaire',
            meta: {requiresAuth: true},
            component: resolve => require(['./components/Global/Auth/Statistiques/ShowTernaire.vue'],resolve),
          }]
    },{
      path: '**',
      redirect: '/'
  }]
})
router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    console.log(localStorage);
    const authUser = localStorage.get('authUser')
    if (authUser && authUser.access_token) {
      next()
    }else{
      next({name: 'login'})
    }
  }else{
    next()
  }
})
export default router



